@import "../../../../../Calendar.variables.scss";

$gap: 5px;
$markerWidth: 5px;

.appointmentCell {
  display: flex;
  flex-direction: row;
  justify-content: space-between;

  height: 100%;

  .leftBlock {
    display: grid;
    column-gap: $gap;

    grid-template-columns: repeat(3, min-content);

    height: 100%;

    span {
      white-space: nowrap;
    }

    .marker {
      width: $markerWidth;
      height: 100%;

      background: green;
    }

    .avatar {
      background: lightgray;
      border-radius: 50%;

      $size: $rowHeight - $gap * 2;

      height: $size;
      width: $size;

      // ${(p: AppointmentCellProps) => {
      //   const size = p.height - parseFloat(gap) * 2;
      //   return `
      //     height: ${size}px;
      //     width: ${size}px;
      //   `;
      // }};
    }

    // .info {
    // }
  }

  .rightBlock {
    height: 100%;

    .points {
      border-radius: 50%;
      background: lightgray;

      // margin-right: ${pointsGap};
      margin-right: 10px;

      // ${(p: AppointmentCellProps) => {
      //   const size = p.height - parseFloat(pointsGap) * 2;
      //   return `
      //     height: ${size}px;
      //     width: ${size}px;
      //   `;
      // }};
    }
  }
}
