.appointmentWrapper {
  background: #fff;
  outline: 1px solid #d3d3d3;

  position: absolute;
  // pointer-events: none;
  user-select: none;

  height: 60px;
  width: 100px;
  transform: "translate(0px, 0px)";

  &.dragging {
    z-index: 10000;
    position: fixed;
    left: 0;
    top: 0;
  }

  &.dragOrigin {
    opacity: 0.3;
  }
}
